:root {
    --overline: #808080;
    --dark-blue:#2A344B;
    --dark-blue2:#20272E;
    --linear-grdient:linear-gradient(90deg, #4561FF 0%, #2A344B 100%);

    --primary: #E8521D;
    --secondary: #4561FF;
    --tertiary: #1FC41E;

    --neutral10: #FFFFFF;
    --neutral20: #F6F7F7;
    --neutral30: #EFF0F1;
    --neutral40: #E3E5E8;
    --neutral50: #C9CCD1;
    --neutral60: #A9ADB6;
    --neutral70: #848B97;
    --neutral80: #737A88;
    --neutral90: #576071;
    --neutral100: #2A344B;
    --neutral110: #1D2840;
}

html {
    overflow-y: scroll;
    scroll-padding: 40px;
}
body {
    background: #FFFFFF;
}

p {
    font-family: 'Poppins';
    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    line-height: 24px;
    color: var(--neutral10);
}  

h1,h2,h3 {
    font-family: 'Inter';
    font-style: normal;
    font-weight: 900;
    color: #FFFFFF;
}

.txt-p-generico {
    font-size: 16px !important;
