@media (min-width : 320px) and (max-width : 650px) {
    h1 {    
        font-size: 35px !important;
        line-height: 38px !important;
    }
}

@media (min-width : 320px  ) and (max-width : 991px)/* Custom, iPhone Retina */  {
    h1 {    
        font-size: 48px;
        line-height: 56px;
    }

    h2 {
        font-weight: 900;
        font-size: 32px;
        line-height: 36px;
    }

    h3 {    
        font-size: 28px;
        line-height: 32px;
    }

    p {
        font-weight: 600 !important;
        font-size: 14px !important;
        line-height: 24px !important;
    }

    .button1 {
        font-family: 'Poppins';
        font-style: normal;
        font-weight: 900 !important;
        font-size: 12px;
        line-height: 18px;
        /* identical to box height */
        
        letter-spacing: 0.16em;
        text-transform: uppercase;

        /* identical to box height, or 100% */
        letter-spacing: 0.16em;
        color: #FFFFFF;
        background-color: var(--primary);
    }

    .button2 {
        font-family: 'Poppins';
        font-style: normal;
        font-weight: 900 !important;
        font-size: 14px;
        line-height: 21px;

        letter-spacing: 0.16em;
        text-transform: uppercase;
    }

    .bullet-title {
        font-size: 14px !important;
    }

    .txt-resposta-pergunta  p {
        font-size: 13px !important;
        font-weight: 500 !important;
        margin-top:10px !important;
    }

    .text-footer {
        font-family: 'Poppins';
        font-style: normal;
        font-weight: 100 !important;
        font-size: 13px;
        line-height: 14px;
    }

    .align-text-footer {
        margin: auto;
    }

    .container-carousel-1{
        padding: 11% 10% 16% 17% !important;
        max-width: 100% !important;
    }
    
    .container-perguntas
    ,.container-gestao-financeirar
    ,.container-abrir-conta 
    ,.sub-container-adaptive-pay
    ,.container-pra-quem-e 
    ,.sub-container-sua-empresa-mais{
        padding: 60px !important;
    }

    .img-question {
        width: 100%;
        max-height: 472.01px;
        left: 0px;

        flex-grow: 0;
        margin: 46px 0px;
        display: flex;
    }  

    .img-client-confiant {
        position: static;
        width: 100% !important;
        height: 100% !important;
        border-radius: 10px;
        margin: 0px 6px;
    }

    .footer-help {
        padding: 20px 20px !important;
    }

    .footer-margin-help {
        margin:1.5rem !important
    }
    
    .sub-container-abrir-conta{
        padding: 24px !important;
    }
    
    .img-moderninha {
        width: 75% !important;
    }

    .img-mockup {
        width: 100% !important;
        height: 100% !important;
        margin:0px !important;
    }
    
    .btn-abrir-conta-cards {
        padding: 0.375rem 0.75rem !important;
    }

    .margin-text-abra-sua-conta {
        margin:auto !important;
    }

    .container-gestao-financeirar {
        background-position: bottom !important;
        background-size: 111% 48% !important;
        background-repeat: no-repeat !important;
    }

    .video-gestao-financeira {
        width: 90% !important;
        height: 90% !important;
    }

    .img-sua-empresa-mais-group {
        width: 100% !important;
    }

    .container-carousel-1 {
        height: 30rem;
        background: transparent !important;
        background-size: 0px 0px !important;
        max-width: 700px;
    }

    .container-pra-quem-e  {
        background-size: auto !important;
    }

    .container-adaptive-pay {
        background-size: auto !important;
        background-repeat: no-repeat !important;
    }

    .carousel-adp {
        background-size: 998px !important
    }
    
    .bnt-carousel {
        margin:auto !important;
    }
    .sub-readline {
        letter-spacing: 1px !important;
    }
}

@media (min-width: 992px)/* Medium Devices, Desktops  */  { 

    h1 {    
        font-size: 48px !important;
        line-height: 56px !important;
    }

    h2 {
        font-size: 48px !important;
        line-height: 56px !important;
    }

    h3 {    
        font-size: 36px !important;
        line-height: 48px !important;
    }

    p {
        font-weight: 600 !important;
    }
    
    .button1 {
        font-family: 'Poppins';
        font-style: normal;
        font-weight: 500;
        font-size: 14px;
        line-height: 14px;

        letter-spacing: 0.16em;
        color: #FFFFFF;
        background-color: var(--primary);
    }

    .button2 {
        font-family: 'Poppins';
        font-style: normal;
        font-weight: 900;
        font-size: 14px;
        line-height: 21px;

        letter-spacing: 0.16em;
        text-transform: uppercase;
    }

    .text-footer {
        font-family: 'Poppins';
        font-style: normal;
        font-weight: 100 !important;
        font-size: 14px;
        line-height: 14px;
    }

    .container-perguntas {
        padding: 120px 48px;
    }

    .img-question {
        min-height: 990.01px !important;
        object-fit: cover;
        left: 120px;
        
        flex-grow: 1;
        margin: 0px 46px 0px 0px;
    }
    
    .text-seus-funcionarios {
        padding-left: 20px;
    }

    .container-adaptive-pay {
        background-size: auto !important;
    }
}

@media (min-width: 1350px)/* Medium Devices, Desktops  */  {

    .carousel-content {
        max-width: 661px;
        max-height: 200px;
    }

    .carousel-adp {
        background-size: auto !important;
    }

    .container-carousel-1
    ,.container-gestao-financeirar
    ,.sub-container-pra-quem-e
    ,.sub-container-adaptive-pay
    ,.sub-container-sua-empresa-mais
    ,.container-abrir-conta 
    ,.container-perguntas
    ,.footer-help {
        max-width: 1440px !important;
    }

    .container-carousel-1
    ,.container-gestao-financeirar
    ,.sub-container-pra-quem-e 
    ,.sub-container-adaptive-pay
    ,.sub-container-sua-empresa-mais
    ,.container-abrir-conta
    ,.container-perguntas {
        padding: 120px 160px !important ;
        margin: auto !important ;
    }

    .footer-help {
        padding: 60px 100px !important;
    }

    .footer {
       padding: 120px 120px 40px !important;
    }

    .footer-margin-help {
        margin: auto 50px 0px 0px !important;
    }

    .sub-container-gestao-financeira {
        margin: 0px 36px 0px 0px !important;
    }

    .img-sua-empresa-mais-group {
        width: 52% !important;
    }

    .img-question {
        min-height: 874.01px !important;
        margin: 0px 46px 0px 0px !important;
    }
}